  display: block;
}
.main-nav__toggle {
  position: absolute;
  top: -41px;
  right: 0;
  border: none;
  background-color: transparent;
  width: 24px;
  height: 16px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
}
.main-nav__toggle--open {
  position: absolute;
  top: -35px;
  right: 0;
  border: none;
  background-color: transparent;
  width: 18px;
  height: 18px;
  padding-right: 3px;
  cursor: pointer;
}
.main-nav__toggle--open::before,
.main-nav__toggle--open::after {
  content: "";
  position: absolute;
  top: -2px;
  left: -6px;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
}
.main-nav__toggle--open::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav__toggle--open::after {
  transform: rotate(-45deg);
}
@media (max-width: 767px) {
  .main-nav--open .site-list__item {
    display: block;
  }
}
.main-nav--nojs .site-list__item {
  display: block;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
